.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.movies {
  display: grid;
  width: 90%;
  padding-top: 70px;
}

@media screen and (min-width: 900px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
    grid-template-columns: repeat(2, minmax(300px, 1fr));
    grid-gap: 50px;
    padding: 50px;
  }

  .movie {
    margin-bottom: 50px;
  }
}

@media screen and (min-width: 1350px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
    grid-template-columns: repeat(3, minmax(300px, 1fr));
    grid-gap: 50px;
    padding: 50px;
  }
}

.goToUp {
  position: fixed;
  right: 40px;
  bottom: 30px;
}
